@font-face {
  font-family: 'Roboto-Light';
  src: url("/fonts/Roboto-Light.woff2") format("woff2");
}

@font-face {
  font-family: 'Roboto-Medium';
  src: url("/fonts/Roboto-Medium.woff2") format("woff2");
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

button {
  border: none;
  background: none;
  padding: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  cursor: pointer;
}

a {
  text-decoration: none;
}

input {
  border: none;
  background: none;
}

.offers {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  width: 100%;
  padding: 50px 15px;
  max-width: 1135px;
  margin: 0 auto;
}

.offer {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-flow: row nowrap;
  flex-flow: row nowrap;
  height: 260px;
  overflow: hidden;
  border-radius: 10px;
  -webkit-box-shadow: 0 2px 14px rgba(0, 0, 0, 0.06);
  box-shadow: 0 2px 14px rgba(0, 0, 0, 0.06);
  margin-bottom: 50px;
}

.offer__photos {
  width: 100%;
  max-width: 414px;
  overflow: hidden;
  margin-right: 20px;
}

.offer__photos img {
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.offer__content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -ms-flex-flow: column nowrap;
  flex-flow: column nowrap;
  padding: 20px 0;
  -webkit-box-pack: start;
  -ms-flex-pack: start;
  justify-content: flex-start;
}

.offer__content__title {
  font-family: 'Roboto-Medium';
  font-size: 18px;
  letter-spacing: .2px;
  margin-bottom: 8px;
  font-weight: 500;
}

.offer__content__address {
  font-family: 'Roboto-Light';
  color: #19a24b;
  margin-bottom: 20px;
  font-size: 18px;
}

.offer__content__address:hover {
  text-decoration: underline;
}

.offer__content__price {
  font-family: 'Roboto-Medium';
  font-size: 28px;
  text-transform: uppercase;
  color: #19a24b;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: end;
  -ms-flex-align: end;
  align-items: flex-end;
}

.offer__content__price span {
  color: #000000;
  margin-left: 5px;
  margin-right: 15px;
}

.offer__content__price p {
  text-transform: none;
  color: #000000;
  font-size: 16px;
  line-height: 28px;
}

.offer__content__relationships {
  margin-top: auto;
  font-family: 'Roboto-Medium';
  color: #000000;
}

.offer__content__like {
  font-family: 'Roboto-Light';
  font-size: 16px;
  margin-top: 15px;
  margin-right: auto;
  color: #000000;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
}

.offer__content__like img {
  margin-right: 5px;
}

.offer__content__like:hover {
  color: #19a24b;
  -webkit-transition: color 0.3s;
  -o-transition: color 0.3s;
  transition: color 0.3s;
}

@media screen and (max-width: 576px) {
  .offers {
    max-width: 576px;
  }

  .offer {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-flow: column nowrap;
    flex-flow: column nowrap;
    height: auto;
  }

  .offer__photos {
    height: 261px;
    max-width: 100%;
  }

  .offer__content {
    padding-left: 20px;
  }
}